<template>
	<view>
		<u-search placeholder="搜索" v-model="search" :show-action="false" @change="getSearchHandle"
			margin="10px 0 10px 0" focus></u-search>
		<view>
			<view v-if="historyList.length > 0" class="history-box">
				<view class="history-list-box">
					<view class="history-list-item" v-for="(item, index) in historyList" :key="index"
						@click="listTap(item)">
						{{isxyzqemp=="0"?item.sharingName:item.cmsName}}
					</view>
					<!-- <ric
					h-text :nodes="item.nameNodes"></rich-text> -->
				</view>
			</view>
			<view v-else class="no-data"> {{search==''? '':'没有搜索到相关内容'}}</view>
		</view>
	</view>
</template>

<script>
	import {
		mapState,
		mapMutations
	} from 'vuex';
	export default {
		components: {

		},
		data() {
			return {
				isxyzqemp:'',
				search: '',
				current: 0,
				historyList: [],
			};
		},
		computed: mapState([ 'hasLogin','uerInfo','uerInfo2']),
		onLoad() {
				//getApp().globalData.isxyzqemp ="1"
				this.isxyzqemp = this.uerInfo2.isxyzqemp
				console.log('ccccc',this.isxyzqemp)
		},

		methods: {
			listTap(item) {
				console.log('item',item)
				if(item.menuId == 16){//业务RPA能力接入Demo
					uni.navigateTo({
						url: '/pages/access/access-znai/ywrpa'
					})
				}else if(item.menuId == 17){//中台NLP系统
					uni.navigateTo({
						url: '/pages/access/access-znai/nlp'
					})
				}else if(item.menuId == 18){//数据建模平台
					uni.navigateTo({
						url: '/pages/access/access-sjzljcfw/sjjmpt'
					})
				}else if(item.menuId == 15){//E-BUS Demo(架构组业务)
					uni.navigateTo({
						url: '/pages/access/access-qllgj/ebus'
					})
				}else if(item.menuId == 13){//金融智能咨询中心
					uni.navigateTo({
						url: '/pages/access/access-dsjjcfw/jrznzxzx/index'
					})
				}else{
					if(item.cmsType == 1){
						//#ifdef APP-PLUS
						plus.runtime.openURL(item.cmsUrl)
						// #endif
						//#ifdef H5
						location.href=item.cmsUrl
						// #endif
					}else if(this.dataForm.cmsType == 2){
						uni.navigateTo({
							url: '/pages/article/article-detail?id='+ (this.isxyzqemp=='0'?item.sharingId : item.cmsId)
						});
					}
				}
				
			},
			getSearchHandle(value) {
				this.request({
					url: "banner/cms/like",
					data: {
						isxyzqemp:this.isxyzqemp,
						name: this.search
					},
				}).then(data => {
					if (data && data.resphead && data.resphead.succeed) {

						console.log('datassssss', data.respbody)
						this.historyList = data.respbody
					} else {
						uni.showToast({
							title: data.resphead.message || "获取数据失败",
							icon: "none"
						});
					}
				});
			},
		}
	};
</script>

<style scoped lang="scss">
	.history-list-box {
		.history-list-item {
			padding: 15px 0;
			margin-left: 15px;
			border-bottom: 1px #EEEEEE solid;
			font-size: 14px;
		}
	}
</style>
